  display: grid;
  grid-template-columns: 70% auto;
  grid-template-rows: 28% auto;
}
.card-modal-header{
  grid-column-start: 1;
  grid-column-end: 3;
}
.card-modal-main{
  grid-column-start: 1;
  grid-column-end: 2;
}
.card-modal-sidebar{
  grid-column-start: 2;
  grid-column-end: 3;
}

.toolbar{
  background-color: rgba(0,0,0,0.15);
  height: 32px;
  padding: 4px;
  position: relative;
  text-align: center;
}
