html,body{
  background-color: rgba(75, 191, 107);
  margin: 0;
}

.list{
  width: 272px;
}

.card{
  background-color: rgba(224,227,230);
  margin: 7% 0 0 7%;
  border-radius: 3px;
  width:272px;
}

.card-header{
  margin: 0px 6px 0px 6px;
  color: rgba(31,57,75);
  font-weight: bold;
  background: transparent;
  border: 1px solid transparent;
}

.card-content{
  background-color: rgba(255,255,255);
  margin: 0px 6px 6px 6px;
  color: rgba(31,57,75);
  border-radius: 3px;
}

.card-content:hover{
  background-color: rgba(245,246,247)
}

.card-footer{
  padding: 0px 6px 0px 6px;
  color: rgba(125,141,152);
}

.card-footer:hover{
  background-color: rgba(197,204,208);
  color: rgba(31,56,75);
  text-decoration: underline;
}

.card-ghost {
    -webkit-transition: -webkit-transform 0.18s ease;
    transition: -webkit-transform 0.18s ease;
    transition: transform 0.18s ease;
    transition: transform 0.18s ease, -webkit-transform 0.18s ease;
    -webkit-transform: rotateZ(5deg);
            transform: rotateZ(5deg)
}

.card-ghost-drop {
    -webkit-transition: -webkit-transform 0.18s ease-in-out;
    transition: -webkit-transform 0.18s ease-in-out;
    transition: transform 0.18s ease-in-out;
    transition: transform 0.18s ease-in-out, -webkit-transform 0.18s ease-in-out;
    -webkit-transform: rotateZ(0deg);
            transform: rotateZ(0deg)
}

#add-list{
  background-color: rgba(95,166,101);
  color: rgba(223,237,224);
  margin: 7% 0 0 7%;
  border-radius: 3px;
}

#add-list:hover{
  background-color: rgba(82,142,88);
}

.new-card-textarea{
  border: 0px;
  width: 93%;
  height:45px;
}

.add-card-btn{
  background-color: #5aac44;
  -webkit-box-shadow: 0 1px 0 0 #3f6f21;
          box-shadow: 0 1px 0 0 #3f6f21;
  border: none;
  color: #fff;
}#trello-img{
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
#toolbar-title{
  font-family: 'Staatliches', cursive;
  color: rgba(222,236,224)
}
#toolbar-github-btn{
  height: 100%;
  float:right;
  background: transparent;
  border: transparent;
  vertical-align: middle;
  height: 100%;
  color: inherit;
  text-decoration: none;
}
#toolbar-github-btn:hover 
{
  color:rgb(33, 82, 14); 
  text-decoration:none; 
  cursor:pointer;  
}
